// core imports
import { FC } from "react"
import CssBaseline from "@mui/material/CssBaseline"
import Container from "@mui/material/Container"
import Grid from "@mui/material/Grid"
import IAppLayout from "./AppLayout.types"
import { Outlet } from "react-router-dom"

// custom imports
import AppNavbar from "./AppNavbar"
import AppFooter from "./AppFooter"

const AppLayout: FC<IAppLayout> = ({ children }) => {
	return (
		<>
			<AppNavbar />
			<CssBaseline />
			<Container maxWidth="xl" sx={({palette}) =>({ backgroundColor: palette.secondary["200"] })}>
				<Grid container display="flex" direction="column" minHeight="100vh">
					<Grid
						item
						// bgcolor="#fff"
						color="#454545"
						flex="1"
						sx={{ padding: "20px" }}
					>
						<Outlet />
					</Grid>
					<AppFooter />
				</Grid>
			</Container>
		</>
	)
}

export default AppLayout
