import {
	Box,
	FormControlLabel,
	IconButton,
	InputAdornment,
	Stack,
	TextField,
	Typography,
	Link
} from "@mui/material"
import { useCallback, useEffect, useRef, useState } from "react"
import { Link as RouterLink, useNavigate } from "react-router-dom"
import AuthContainer from "../../components/AuthContainer"
import InputStatus from "../../components/InputStatus"
import StyledButton from "../../components/StyledButton"
import StyledSwitch from "../../components/StyledSwitch"
import ScrollableStack from "../../layout/ScrollableStack"
import { VisibilityOff, Visibility } from "../../utils/Icons"
import {
	ILoginError,
	IFormData,
	ILoginResponse,
	IRedirectionResponse,
	ISteps,
	IVerifyEmailError,
	IVerifyEmailResponse
} from "./Login.types"
import { TextFieldStyled } from "./index.styles" //NOSONAR
import AccountsList from "../../components/AccountsList/AccountsList"
import useLocalStorage from "../../hooks/useLocalStorage"
import { useForm, FormProvider } from "react-hook-form"
import { MutationFunction, useMutation, UseMutationResult } from "react-query"
import { post } from "../../api/utils"
import { AxiosError, AxiosResponse } from "axios"
import { api } from "../../api/config"
import EmailVerify from "../../components/AuthComponents/EmailVerify"
import LoginVerify from "../../components/AuthComponents/LoginVerify"
import { DevTool } from "@hookform/devtools"
import { loginSchema } from "./LoginValidations"
import { yupResolver } from "@hookform/resolvers/yup"
import RedirectToSp from "../../components/AuthComponents/RedirectToSp"

const Login = () => {
	const [step, setStep] = useState<ISteps>("email-verifier")
	const [redirectMeta, setRedirectMeta] = useState({
		redirect_url: "",
		saml_response: ""
	})
	let navigate = useNavigate()
	let { setLocalStorage } = useLocalStorage("access_token")
	let { setLocalStorage: setRefreshToken } = useLocalStorage("refresh_token")
	let formRef = useRef<HTMLFormElement>(null)

	const { register, handleSubmit, control, formState, setError, ...rest } =
		useForm<IFormData>({
			defaultValues: {
				email: "",
				password: ""
			},
			context: {
				step
			},
			resolver: yupResolver(loginSchema)
		})

	const methods = {
		register,
		control,
		handleSubmit,
		setError,
		formState,
		...rest
	}

	console.log("formState", formState)

	function emailMutation(formdata: Omit<IFormData, "password">) {
		return post<IVerifyEmailResponse>("/email-verifier", formdata, { withCredentials: true })
	}

	function loginMutation(formdata: IFormData) {
		return post<ILoginResponse>("/login", formdata, { withCredentials: true })
	}
	//STEP 1 : email-verifier
	const {
		mutate,
		error,
		isLoading
	}: UseMutationResult<
		IVerifyEmailResponse,
		AxiosError,
		Omit<IFormData, "password">
	> = useMutation<
		IVerifyEmailResponse,
		AxiosError,
		Omit<IFormData, "password">
	>(emailMutation, {
		onSuccess: ({ data: response, status: resStatus }) => {
			console.log("Res:", response, resStatus, typeof resStatus)
			const { message, verification } = response
			if (resStatus === 200 && verification && message === "success") {
				setStep("login")
			}
		},
		onError: ({ message }) => {
			console.log("Error Here:", error)
			setError("email", { type: "server", message })
		}
	})

	//STEP 2 : Login
	const {
		mutate: _mutate,
		isError: _isError,
		data: _data,
		error: _error,
		isLoading: _isLoading,
		status: _status
	}: UseMutationResult<ILoginResponse, AxiosError, IFormData> = useMutation<
		ILoginResponse,
		AxiosError<IRedirectionResponse>,
		IFormData
	>(loginMutation, {
		onSuccess: ({ data: response, status }) => {
			console.log("Res:", response, status, typeof status)
			const {
				access_token,
				refresh_token,
				statuscode,
			} = response
			//check for redirect request
			if (statuscode === 200) {
				setLocalStorage(access_token)
				setRefreshToken(refresh_token)
				navigate("/")
			}
		},
		onError: (err) => {
			const { message, response } = err
			if(!response) {
				return
			}
			const { data } = response
			
			const {
				redirect_url,
				saml_response,
				access_token,
				refresh_token
			} = data

			if (response.status === 307 && redirect_url && saml_response) {
				setRedirectMeta({ redirect_url, saml_response })
				setLocalStorage(access_token)
				setRefreshToken(refresh_token)
				setStep("redirect")
			}
			console.log("message", message)
			setError("password", { type: "server", message })
		}
	})

	const onSubmit = handleSubmit(submittedData => {
		console.log(submittedData)
		const { email, password } = submittedData
		if (step === "email-verifier") {
			mutate({ email })
		}
		if (step === "login") {
			console.log("LOGIN")
			_mutate({ email, password })
		}
	})

	return (
		<FormProvider {...methods}>
			<DevTool control={control} />
			<form
				ref={formRef}
				{...(redirectMeta?.redirect_url && {
					action: redirectMeta?.redirect_url,
					method: "POST"
				})}
				{...( step !== "redirect" && { onSubmit: onSubmit})}
			>
				<AuthContainer loading={isLoading || _isLoading}>
					<Stack spacing={2}>
						{step === "email-verifier" && <EmailVerify />}
						{step == "login" && <LoginVerify />}
						{step == "redirect" && (
							<RedirectToSp
								redirect_url={redirectMeta.redirect_url}
								saml_response={redirectMeta.saml_response}
								formRef={formRef}
							/>
						)}

						<Box sx={{ display: "flex", width: "100%" }}>
							{step == "login" && (
								<Box
									sx={{
										marginRight: "auto",
										display: "flex",
										alignItems: "center"
									}}
								>
									<FormControlLabel
										sx={{ marginLeft: 0, gap: "0.5rem" }}
										control={<StyledSwitch defaultChecked />}
										label="Stay signed in"
									/>
								</Box>
							)}

							<StyledButton
								type="submit"
								disabled={isLoading || _isLoading}
								sx={{ marginLeft: "auto" }}
								variant="contained"
							>
								{step != "accounts" ? "Continue" : "Verify"}
							</StyledButton>
						</Box>
						{step === "login" && (
							<Link
								component={RouterLink}
								color="primary"
								underline="none"
								variant="body1"
								to="/forgot-password"
							>
								Forgot password?
							</Link>
						)}
					</Stack>
				</AuthContainer>
			</form>
		</FormProvider>
	)
}
export default Login
