import { TextField, Typography } from "@mui/material"
import React from "react"
import { useFormContext } from "react-hook-form"
import ScrollableStack from "../../layout/ScrollableStack"
import FormInput from "../FormInput/FormInput"
import InputStatus from "../InputStatus"
import { IEmailVerfiy } from "./Authcomponents.types"

const EmailVerify = ({ isError, error }: IEmailVerfiy) => {
	const { register, formState } = useFormContext()
	const { errors } = formState
	console.log("errors", errors)
	const emailError = errors?.["email"]?.message
	return (
		<>
			<Typography variant="h5" fontWeight={600}>
				Sign in
			</Typography>
			<ScrollableStack spacing={2}>
				<Typography variant="body1">Continue to Ez Login</Typography>
				{/* <TextField
					{...register("email")}
					placeholder="Email"
					type="email"
					fullWidth
					variant="outlined"
					error={!!emailError}
					helperText={!!emailError && <InputStatus text={emailError} />}
				/> */}
				<FormInput
					placeholder="Email"
					type="email"
					fullWidth
					variant="outlined"
					name="email"
				/>
			</ScrollableStack>
		</>
	)
}

export default EmailVerify
